/* Palette de couleurs */
:root {
  --primary-color: #A22A2A; /* Rouge inspiré du texte "SENTEUR" */
  --secondary-color: #F5EDE3; /* Beige doux pour le fond */
  --accent-color: #000000; /* Noir pour les textes principaux */
  --highlight-color: #8C756A; /* Brun clair pour les détails */
  --hover-color: #E2C9B0; /* Beige légèrement foncé pour les survols */
}

/* Règles générales */
body {
  font-family: 'Playfair Display', serif; /* Élégant et classique */
  color: var(--accent-color);
  background-color: var(--secondary-color);
  margin: 0;
  padding: 0;
  line-height: 1.6;
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Playfair Display', serif;
  color: var(--primary-color);
  margin: 0;
  padding: 0.5rem 0;
}

p {
  font-size: 1rem;
  color: var(--accent-color);
  margin: 0;
  padding: 0.5rem 0;
}

a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: var(--hover-color);
}

button {
  background-color: var(--primary-color);
  color: var(--secondary-color);
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  font-family: 'Playfair Display', serif;
  cursor: pointer;
  border-radius: 5px;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: var(--hover-color);
  color: var(--accent-color);
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.container {
  width: 90%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}
